class ColorBox extends React.Component {
  render() {
    return <span className="colorbox" style={{"backgroundColor": this.props.color}}/>
  }
}

class ColorList extends React.Component {
  constructor() {
    super();
    this.generateClickHandler = this.generateClickHandler.bind(this);
  }
  
  generateClickHandler(c) {
    return (()=>this.props.addFunc(c));
  }
  
  render() {
    return(
      <div className="colorlist-scroller">
        <ul className="colorlist">
          { Object.keys(colormap).filter((c)=>
            (c.toLowerCase().includes(this.props.searchDMC.toLowerCase()) || colormap[c].Name.toLowerCase().includes(this.props.searchDMC.toLowerCase())))
              .map((c)=><li key={ c } className="colorlist-item" onClick={ this.generateClickHandler(c) }>
                 <span className="colorlist-text">
                   <ColorBox color={ colormap[c].Color } />
                 </span>
                 <span className="colorlist-text">{ c }</span>
                 <span className="colorlist-text">{ colormap[c].Name }</span>
               </li>)
          }
        </ul>
      </div>
    );
  }
}

class ColorSearcher extends React.Component {
  constructor() {
    super();
    this.state = {searchDMC: "", searchName: ""};
    this.handleSearchDMC = this.handleSearchDMC.bind(this);
    this.handleSearchName = this.handleSearchName.bind(this);
  }
  
  handleSearchDMC(event) {
    this.setState(Object.assign({}, this.state, {searchDMC: event.target.value}));
  }
  
  handleSearchName(event) {
    this.setState(Object.assign({}, this.state, {searchName: event.target.value}));
  }
  
  render() {
    return(
      <div className="colorsearcher">
        <fieldset>
          <legend>Colors list (click to add)</legend>
        <span className="colorlist-header-row">
        <span className="colorlist-header">Color preview</span>
        <span className="colorlist-search">
          <input className="fullwidth" placeholder="Search by DMC number or name" onInput={ this.handleSearchDMC }/>
        </span>
        </span>
        <ColorList searchDMC = { this.state.searchDMC } searchName={ this.state.searchName } addFunc={ this.props.addFunc }/>
      </fieldset>
      </div>
    );
  }
}

class ColorPalette extends React.Component {
  constructor() {
    super();
    this.state = {name: "", columns: 4, key: "dmc"};
    this.generateRemoveFunction = this.generateRemoveFunction.bind(this);
    this.setName = this.setName.bind(this);
    this.setColumns = this.setColumns.bind(this);
    this.setKey = this.setKey.bind(this);
    this.generatePalette = this.generatePalette.bind(this);
  }
  
  generateRemoveFunction(index) {
    return (()=>this.props.removeFunc(index))
  }
  
  setName(event) {
    this.setState(Object.assign({}, this.state, {name: event.target.value}));
  }
  
  setColumns(event) {
    if (event.target.value) {
      this.setState(Object.assign({}, this.state, {columns: event.target.value}));
    }
  }
  
  setKey(event) {
    this.setState(Object.assign({}, this.state, {key: event.target.value}));
  }
  
  hexToDec(hexColor) {
    const red = parseInt(hexColor.slice(1, 3), 16).toString().padStart(3, " ");
    const green = parseInt(hexColor.slice(3, 5), 16).toString().padStart(3, " ");
    const blue = parseInt(hexColor.slice(5, 7), 16).toString().padStart(3, " ");
    return `${red} ${green} ${blue}`;
  }
  
  // https://stackoverflow.com/questions/3665115
  download(filename, text) {
    var element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);

    element.style.display = 'none';
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
  }
  
  generatePalette() {
    if (this.props.colors.length === 0) {
      alert("Add some colors first");
      return;
    }
    if (!this.state.name) {
      alert("Set the palette name before generating");
      return;
    }
    let output = "GIMP Palette\n";
    output += `Name: ${this.state.name}\n`;
    output += `Columns: ${this.state.columns}\n`;
    output += "#\n# Generated with Robbie's DMC GIMP Palette Generator\n#\n";
    if (this.state.key === "dmc") {
      this.props.colors.forEach((c)=>output += `${this.hexToDec(colormap[c].Color)} ${c}\n`);
    } else {
      this.props.colors.forEach((c)=>output += `${this.hexToDec(colormap[c].Color)} ${colormap[c].Name}\n`);
    }
    this.download(`${this.state.name}.gpl`, output);
  }
  
  render() {
    return (
      <div className="colorpalette">
        <fieldset>
          <legend>Colors (click to remove)</legend>
        <div className="colorholder" style={{"maxWidth": `${this.state.columns}rem`}}>
          { this.props.colors.map((c, index)=><PaletteElement key={ c } colorId={ c } onClick={ this.generateRemoveFunction(index) } />) }
        </div>
        </fieldset>
      <fieldset>
        <legend>Palette generation settings</legend>
        <table>
          <tbody>
	    <tr>
	      <td>
		Palette name:
	      </td>
	      <td>
		<input className="fullwidth" value={this.state.name} onChange={ this.setName } />
	      </td>
	    </tr>
	    <tr>
	      <td>
		Number of columns:
	      </td>
	      <td>
		<input className="fullwidth" type="number" placeholder="Number of columns" value={this.state.columns} onChange={ this.setColumns }/>
	      </td>
	    </tr>
	    <tr>
	      <td>
		Color names in palette:
	      </td>
	      <td>
	       <select className="fullwidth" onInput={ this.setKey }>
		 <option value="dmc">Use DMC number</option>
		 <option value="name">Use color name</option>
	       </select>
	      </td>
	    </tr>
          </tbody>
        </table>
      </fieldset>
      <button onClick={ this.generatePalette }>Generate GIMP Palette</button>
      </div>
    );
  }
}

class PaletteElement extends React.Component {
  render() {
    return <span className="colorbox small" style={{"backgroundColor": colormap[this.props.colorId].Color}} onClick={ this.props.onClick } title={ `${this.props.colorId}: ${colormap[this.props.colorId].Name}` }/>
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state={chosenColors: []};
    this.addColor = this.addColor.bind(this);
    this.removeColor = this.removeColor.bind(this);
  }
  
  addColor(c) {
    if (!this.state.chosenColors.includes(c)) {
      this.setState(Object.assign({}, this.state, {chosenColors: this.state.chosenColors.concat(c)}));
    }
  }
  
  removeColor(i) {
    this.setState(Object.assign({}, this.state, {chosenColors: [...this.state.chosenColors.slice(0, i), ...this.state.chosenColors.slice(i + 1)]}))
  }
  
  render() {
    return(
      <div>
	<h2>DMC Thread Color GIMP Palette generator</h2>
	<div>
	  <ColorSearcher addFunc={ this.addColor }/>
	  <ColorPalette colors={ this.state.chosenColors } removeFunc={ this.removeColor } />
	</div>
        <span>Colors taken from <a href="http://www.camelia.sk">http://www.camelia.sk</a></span>
      </div>
    );
  }
}


export default App
